import React, { Component } from 'react'
import Autosuggest from 'react-autosuggest'
import './CountrySearch.css'
import { navigate } from 'gatsby'
import countries_json from './countries_json.json'
import { trackCustomEvent } from 'gatsby-plugin-google-analytics'

function recordCountrySelection(countryName) {
  trackCustomEvent({
    // string - required - The object that was interacted with (e.g.video)
    category: 'Main Search',
    // string - required - Type of interaction (e.g. 'play')
    action: 'Selection',
    // string - optional - Useful for categorizing events (e.g. 'Spring Campaign')
    label: countryName,
    // number - optional - Numeric value associated with the event. (e.g. A product ID)
    // value: 43
  })
}

function escapeRegexCharacters(str) {
  return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')
}

function shouldRenderSuggestions(value) {
  return value.trim().length > 1
}

function getSuggestions(value) {
  const escapedValue = escapeRegexCharacters(value.trim())

  if (escapedValue === '' || escapedValue.length < 2) {
    return []
  }

  const regex = new RegExp('^' + escapedValue, 'i')

  return countries_json.filter(country => {
    var res = regex.test(country.name)
    if (country.otherNames != null) {
      country.otherNames.forEach(
        otherName => (res = res || regex.test(otherName))
      )
    }
    return res
  })
}

function getSuggestionValue(suggestion) {
  return suggestion.name
}

function renderSuggestion(suggestion) {
  return <span>{suggestion.name}</span>
}

function suggestionSelected(
  event,
  { suggestion, suggestionValue, suggestionIndex, sectionIndex, method }
) {
  recordCountrySelection(suggestion.name)
  navigate(suggestion.path)
}

class Search extends React.Component {
  constructor() {
    super()

    this.state = {
      value: '',
      suggestions: [],
    }
  }

  onChange = (event, { newValue, method }) => {
    this.setState({
      value: newValue,
    })
  }

  onSuggestionsFetchRequested = ({ value }) => {
    this.setState({
      suggestions: getSuggestions(value),
    })
  }

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: [],
    })
  }

  render() {
    const { value, suggestions } = this.state
    const inputProps = {
      placeholder: 'Country',
      value,
      onChange: this.onChange,
      autoFocus: true,
    }

    return (
      <Autosuggest
        suggestions={suggestions}
        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
        onSuggestionsClearRequested={this.onSuggestionsClearRequested}
        getSuggestionValue={getSuggestionValue}
        renderSuggestion={renderSuggestion}
        inputProps={inputProps}
        onSuggestionSelected={suggestionSelected}
        highlightFirstSuggestion={true}
        shouldRenderSuggestions={shouldRenderSuggestions}
      />
    )
  }
}

export default Search
